Moment.locale("en");
momentLocalizer();

const tableIcons = {
  Add: forwardRef((props, ref) => <AddBox {...props} ref={ref} />),
  Check: forwardRef((props, ref) => <Check {...props} ref={ref} />),
  Clear: forwardRef((props, ref) => <Clear {...props} ref={ref} />),
  Delete: forwardRef((props, ref) => <DeleteOutline {...props} ref={ref} />),
  DetailPanel: forwardRef((props, ref) => (
    <ChevronRight {...props} ref={ref} />
  )),
  Edit: forwardRef((props, ref) => <Edit {...props} ref={ref} />),
  Export: forwardRef((props, ref) => <SaveAlt {...props} ref={ref} />),
  Filter: forwardRef((props, ref) => <FilterList {...props} ref={ref} />),
  FirstPage: forwardRef((props, ref) => <FirstPage {...props} ref={ref} />),
  LastPage: forwardRef((props, ref) => <LastPage {...props} ref={ref} />),
  NextPage: forwardRef((props, ref) => <ChevronRight {...props} ref={ref} />),
  PreviousPage: forwardRef((props, ref) => (
    <ChevronLeft {...props} ref={ref} />
  )),
  ResetSearch: forwardRef((props, ref) => <Clear {...props} ref={ref} />),
  Search: forwardRef((props, ref) => <Search {...props} ref={ref} />),
  SortArrow: forwardRef((props, ref) => <ArrowUpward {...props} ref={ref} />),
  ThirdStateCheck: forwardRef((props, ref) => <Remove {...props} ref={ref} />),
  ViewColumn: forwardRef((props, ref) => <ViewColumn {...props} ref={ref} />),
};

const useStyles = makeStyles((theme) => ({
  card: {
    margin: theme.spacing(20),
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
  },
  form: {
    width: "100%", // Fix IE 11 issue.
    marginTop: theme.spacing(3),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
  cardBottom: {
    marginBottom: 20,
  },
  Select: {
    height: 45,
    width: 350,
  },
  button: {
    margin: theme.spacing(1),
  },

  root: {
    "& > *": {
      margin: theme.spacing(1),
    },
  },
  input: {
    display: "none",
  },
  error: {
    color: "#f85032",
    fontSize: "11px",
  },
  success: {
    color: "#4BB543 ",
    fontSize: "11px",
  },
}));

const HepatitisPatients = (props) => {
  //   const [permissions, setPermissions] = useState(props.permissions);
  const calculate_age = (dob) => {
    const today = new Date();
    const dateParts = dob.split("-");
    const birthDate = new Date(dob); // create a date object directlyfrom`dob1`argument
    let age_now = today.getFullYear() - birthDate.getFullYear();
    const m = today.getMonth() - birthDate.getMonth();

    if (age_now <= 0 && m < 0 && today.getDate() < birthDate.getDate()) {
      age_now--;
    }

    if (age_now === 0) {
      return m + " month(s)";
    }
    return age_now + " year(s)";
  };
  const [loading, setLoading] = useState("");
  const [enablePPI, setEnablePPI] = useState(true);
  const tableRef = useRef(null);

  const getHospitalNumber = (identifier) => {
    const hospitalNumber = identifier.identifier.find(
      (obj) => obj.type == "HospitalNumber"
    );
    return hospitalNumber ? hospitalNumber.value : "";
  };
  const handleRemoteData = (query) =>
    new Promise((resolve, reject) => {
      axios
        .get(
          `${baseUrl}hepatitis?pageSize=${query.pageSize}&pageNo=${query.page}&searchParam=${query.search}`,
          { headers: { Authorization: `Bearer ${token}` } }
        )
        .then((response) => response)
        .then((result) => {
          console.log(result.data);
          if (result.data === "") {
            resolve({
              data: [],
              page: 0,
              totalCount: 0,
            });
          } else {
            resolve({
              data: result.data.map((row) => ({
                name: [row?.firstName, row?.otherName, row?.surname]
                  .filter(Boolean)
                  .join(", "),
                id: row?.hospitalNumber,
                sex: row?.sex
                  ? row.sex.toLowerCase().charAt(0).toUpperCase() +
                    row.sex.slice(1).toLowerCase()
                  : row.gender.toLowerCase().charAt(0).toUpperCase() +
                    row.gender.slice(1).toLowerCase(),
                dateOfBirth: row.dateOfBirth,
                age:
                  row.dateOfBirth === 0 ||
                  row.dateOfBirth === undefined ||
                  row.dateOfBirth === null ||
                  row.dateOfBirth === ""
                    ? 0
                    : calculate_age(row.dateOfBirth),
                actions: (
                  <div>
                    <Link
                      to={{
                        pathname: "/patient-history",
                        state: {
                          patientId: row.id,
                          patientObj: row,
                        },
                      }}
                    >
                      <ButtonGroup
                        variant="contained"
                        aria-label="split button"
                        style={{
                          backgroundColor: "rgb(153, 46, 98)",
                          height: "30px",
                          width: "215px",
                        }}
                        size="large"
                      >
                        <Button
                          color="primary"
                          size="small"
                          aria-label="select merge strategy"
                          aria-haspopup="menu"
                          style={{ backgroundColor: "rgb(153, 46, 98)" }}
                        >
                          <TiArrowForward />
                        </Button>
                        <Button style={{ backgroundColor: "rgb(153, 46, 98)" }}>
                          <span
                            style={{
                              fontSize: "12px",
                              color: "#fff",
                              fontWeight: "bolder",
                            }}
                          >
                            Patient Dashboard
                          </span>
                        </Button>
                      </ButtonGroup>
                    </Link>

                    {/* {permissions.includes("view_patient") ||
                    permissions.includes("all_permission") ? (
                      <SplitActionButton actions={actionItems(row)} />
                    ) : (
                      ""
                    )} */}
                  </div>
                ),
              })),
              page: query.page,
              totalCount: result.data.totalRecords,
            });
          }
        });
    });
  const enablePPIColumns = () => {
    setEnablePPI(!enablePPI);
  };

  const PPISelect = () => (
    <div>
      <div className="form-check custom-checkbox  float-left mt-4 mb-4 ">
        <input
          type="checkbox"
          className="form-check-input"
          name="showPP!"
          id="showPP"
          value="showPP"
          checked={enablePPI === true ? false : true}
          onChange={enablePPIColumns}
          style={{
            border: "1px solid #014D88",
            borderRadius: "0.25rem",
          }}
        />
        <label className="form-check-label" htmlFor="basic_checkbox_1">
          <b style={{ color: "#014d88", fontWeight: "bold" }}>SHOW PII</b>
        </label>
      </div>

      {/* {permissions.includes("view_patient") ||
        permissions.includes("all_permission") ? (
          <FormGroup className=" float-right mr-1">
            <FormControlLabel
              control={
                <Checkbox
                  onChange={enablePPIColumns}
                  checked={!enablePPI}
                  style={{ color: "#014d88", fontWeight: "bold" }}
                />
              }
              label="Show PII"
              style={{ color: "#014d88", fontWeight: "bolder" }}
            />
          </FormGroup>
        ) : (
          <h5 style={{ color: "#3d4465", fontWeight: "bold" }}>Patients</h5>
        )} */}
    </div>
  );

  const handleChangePage = (page) => {
    setCurrentPage(page + 1);
  };
  return (
    <div>
      <MaterialTable
        tableRef={tableRef}
        /*onSearchChange={(e) => {
                    handleSearchChange(e);
                }}*/
        icons={tableIcons}
        title={<PPISelect />}
        columns={[
          {
            title: "Name",
            field: "name",
            filtering: false,
            hidden: enablePPI,
          },
          { title: "Hosp. Number", field: "id", filtering: false },
          { title: "Sex", field: "sex", filtering: false },
          { title: "Date Of Birth", field: "dateOfBirth", filtering: false },
          { title: "Age", field: "age", filtering: false },
          /*{ title: "Address", field: "address", filtering: false },*/
          /*{ title: "Status", field: "status", filtering: false },*/
          { title: "Actions", field: "actions", filtering: false },
        ]}
        isLoading={loading}
        data={handleRemoteData}
        options={{
          headerStyle: {
            backgroundColor: "#014d88",
            color: "#fff",
            fontSize: "16px",
            padding: "10px",
            fontWeight: "bolder",
          },
          searchFieldStyle: {
            width: "50%",
          },
          filtering: false,
          exportButton: false,
          searchFieldAlignment: "left",
          pageSizeOptions: [10, 20, 100],
          pageSize: 10,
          debounceInterval: 400,
          sorting: true,
        }}
        onChangePage={handleChangePage}
        //localization={localization}
      />
      {/* <MaterialTable
        icons={tableIcons}
        title="Find Patient "
        columns={[
          // { title: " ID", field: "Id" },
          {
            title: "Patient Number",
            field: "hospital_number",
            filtering: false,
          },
          {
            title: "Name",
            field: "name",
          },
          { title: "Sex", field: "gender", filtering: false },
          { title: "Age", field: "age", filtering: false },

          { title: "Actions", field: "actions", filtering: true },
        ]}
        data={(query) =>
          new Promise((resolve, reject) =>
            axios
              .get(
                `${baseUrl}covid/all-patients?pageSize=${query.pageSize}&pageNo=${query.page}&searchValue=${query.search}`,
                { headers: { Authorization: `Bearer ${token}` } }
              )
              .then((response) => response)
              .then((result) => {
                resolve({
                  data: result?.data?.records?.map?.((row) => ({
                    name: row.firstName + " " + row.lastName,
                    hospital_number: row.participantId,
                    gender: row.gender !== null ? row.gender.display : "",
                    age: calculate_age(row.dob),

                    count: (
                      <Label color="blue" size="mini">
                        {row.vaccinationStatus}
                      </Label>
                    ),

                    actions: (
                      <div>
                        <Link
                          to={{
                            pathname: "/patient-history",
                            state: { patientObj: row },
                          }}
                        >
                          <ButtonGroup
                            variant="contained"
                            aria-label="split button"
                            style={{
                              backgroundColor: "rgb(153, 46, 98)",
                              height: "30px",
                              width: "215px",
                            }}
                            size="large"
                          >
                            <Button
                              color="primary"
                              size="small"
                              aria-label="select merge strategy"
                              aria-haspopup="menu"
                              style={{ backgroundColor: "rgb(153, 46, 98)" }}
                            >
                              <MdDashboard />
                            </Button>
                            <Button
                              style={{ backgroundColor: "rgb(153, 46, 98)" }}
                            >
                              <span
                                style={{
                                  fontSize: "12px",
                                  color: "#fff",
                                  fontWeight: "bolder",
                                }}
                              >
                                Patient Dashboard
                              </span>
                            </Button>
                          </ButtonGroup>
                        </Link>
                      </div>
                    ),
                  })),
                  page: query.page,
                  totalCount: result.data.totalRecords,
                });
              })
          )
        }
        options={{
          headerStyle: {
            backgroundColor: "#014d88",
            color: "#fff",
          },
          searchFieldStyle: {
            width: "200%",
            margingLeft: "250px",
          },
          filtering: false,
          exportButton: false,
          searchFieldAlignment: "left",
          pageSizeOptions: [10, 20, 100],
          pageSize: 10,
          debounceInterval: 400,
        }}
      /> */}
    </div>
  );
};

export default HepatitisPatients;
